import React from 'react'

class OcrResult extends React.Component {

    constructor() {
        super()
        this.state = {
            ocrState: "",
            percentCompleted: 0,
            shopName: "",
            totalAmount: ""

        }
    }

    componentDidMount() {
        this.scanPicture(this.props.shopName, this.props.totalAmount)
    }

    scanPicture(pic1, pic2) {
        let tesseract = window.Tesseract

        tesseract
            .recognize(pic1, {lang: 'eng'})
            .progress(message => {
                if(message.status === "recognizing text") {
                    this.setState({ocrState: message.status, percentCompleted: message.progress})
                } else {
                    this.setState({ocrState: message.status})
                }
            })
            .then(result => {
                this.setState({shopName: result.text})
            })

        tesseract
            .recognize(pic2, {lang: 'eng'})
            .progress(message => {
                if(message.status === "recognizing text") {
                    this.setState({ocrState: message.status, percentCompleted: message.progress})
                } else {
                    this.setState({ocrState: message.status})
                }
            })
            .then(result => {
                this.setState({totalAmount: result.text})
            })
    }

    render() {
        return (
            <div className="OcrResult">
                {/* <img src={props.picture} alt=""/> */}
                <div className="status">
                    <span>{this.state.ocrState.toLocaleUpperCase()}</span>
                </div>
                <div className="progress">
                    <div className="progress-bar" style={{width: `${this.state.percentCompleted*100}%`}}></div>
                </div>
                <div className="result">
                    <pre>
                        shopname: {this.state.shopName}
                        totalamount: {this.state.totalAmount}
                    </pre>
                </div>
                {/* <button className="btn btn-danger btn-block" onClick={props.scanAgain}>Scan again</button> */}
            </div>
        )
    }
}

export default OcrResult