import React from 'react'
import ReactCrop from 'react-image-crop'
import 'react-image-crop/dist/ReactCrop.css'
import OcrResult from './OcrResult'

class CropImage extends React.Component {

    constructor() {
        super()
        this.state = {
            crop: {
                x: 0,
                y: 0
            },
            showModal: false,
            croppedTitle: null,
            croppedAmount: null, 
            finished: false
        }
    }

    onChange = (crop) => {
        this.setState({crop})
    }

    onComplete = (crop, pixelCrop) => {
        let image = new Image()
        image.src = this.props.picture
        let cropped = this.getCroppedImg(image, pixelCrop)
        if(this.state.croppedTitle === null) {
            this.setState({croppedTitle: cropped, showModal: true})
        } else {
            this.setState({croppedAmount: cropped, showModal: true})
        }

    }

    getCroppedImg(image, pixelCrop) {
        const canvas = document.createElement('canvas')
        canvas.width = pixelCrop.width
        canvas.height = pixelCrop.height
        const ctx = canvas.getContext('2d')

        ctx.drawImage(
            image,
            pixelCrop.x,
            pixelCrop.y,
            pixelCrop.width,
            pixelCrop.height,
            0,
            0,
            pixelCrop.width,
            pixelCrop.height
        )
        return canvas.toDataURL('image/jpeg')
    }

    openModal = () => {
        this.setState({showModal: true})
    }

    cancelModal = () => {
        if(this.state.croppedAmount === null) {
            this.setState({crop: {x: 0, y: 0}, showModal: false, croppedTitle: null})
        } else {
            this.setState({crop: {x: 0, y: 0}, showModal: false, croppedAmount: null})
        }
    }

    confirm = () => {
        this.setState({crop: {x: 0, y: 0}, showModal: false})
        console.log(this.state)
        if(this.state.croppedAmount !== null && this.state.croppedTitle !== null) {
            this.setState({finished: true})
        }
    }

    render() {

        const modal = <div className="modal" style={{display: "block"}}>
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title">Confirm crop?</h5>
                        <button type="button" className="close" onClick={this.cancelModal}>
                            <span>&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <p>Do you want to save this as the {this.state.croppedAmount === null ? "shop name" : "total amount"}?</p>
                        {this.state.croppedAmount === null ? 
                            <img src={this.state.croppedTitle} /> 
                            :
                            <img src={this.state.croppedAmount} />

                        }
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-primary" onClick={this.confirm}>Confirm</button>
                        <button type="button" className="btn btn-secondary" onClick={this.cancelModal}>Close</button>
                    </div>
                </div>
            </div>
        </div>

        return(
            <div>
                {this.state.finished ? 
                    <OcrResult shopName={this.state.croppedTitle} totalAmount={this.state.croppedAmount}/> 
                    :
                    <div className="CropImage">
                        {this.state.showModal ? modal : (null)}
                        <ReactCrop src={this.props.picture} onComplete={this.onComplete} onChange={this.onChange} crop={this.state.crop}/>
                        <h3 className="font-weight-light">Shop name</h3>
                        <img src={this.state.croppedTitle} alt=""/>
                        <h3 className="font-weight-light">Total price</h3>
                        <img src={this.state.croppedAmount} alt=""/>
                        <button className="btn btn-danger btn-block" onClick={this.props.retake}>Retake photo</button>
                    </div>
                }
            </div>
            
            
        )
    }
}



export default CropImage