import React from 'react'
import Webcam from 'react-webcam'
import CropImage from './CropImage';


class Ocr extends React.Component {

    constructor() {
        super()
        this.state = {
            image: null
        }
    }

    setRef = webcam => {
        this.webcam = webcam
    }

    capture = () => {
        const imageSrc = this.webcam.getScreenshot()
        this.setState({image: imageSrc})
    }

    scanAgain = () => {
        this.setState({
            image: null
        })
    }

    render() {
        const videoConstraints = {
            facingMode: {exact: "environment"}
        }
        return(
            <div className='Ocr p-4'>
                {this.state.image === null ? 
                    <div className="camera">
                        <Webcam 
                            audio={false} 
                            width='100%'
                            ref={this.setRef} 
                            // videoConstraints={videoConstraints}
                            screenshotFormat="image/jpeg"
                            ></Webcam>
                        <button className="btn btn-success btn-block" onClick={this.capture}>Capture</button>
                        
                    </div>
                    :
                    <CropImage picture={this.state.image} retake={this.scanAgain}/>
                    // <OcrResult ocrState={this.state.ocrState}
                    //     percentCompleted={this.state.percentCompleted}
                    //     result={this.state.result}
                    //     scanAgain = {this.scanAgain}
                    //     picture={this.state.image}/>
                }
                
                
                

            </div>
        )
    }
}

export default Ocr